import React, {useState,useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CardEditMenu from './CardEditMenu.js'
import Avatar from '@material-ui/core/Avatar';
import SearchBox from 'components/SearchBox/index';
import {base_url, public_url, $http} from "config";
import {useHistory} from 'react-router-dom';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import AttachFile from '@material-ui/icons/AttachFile';
import {Modal, ModalHeader} from 'reactstrap';
import Button from '@material-ui/core/Button';
import CloseIcon from '@material-ui/icons/Close';
import Fab from '@material-ui/core/Fab';


const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "first_name", align: true, disablePadding: false, label: "Name"},
  {id: "job_title", align: true, disablePadding: false, label: "Job Title"},
  {id: "company_name", align: true, disablePadding: false, label: "Company"},
  {id: "email", align: true, disablePadding: false, label: "Email"},
  {id: "status", align: true, disablePadding: false, label: "Status"},
  {id: "Actions", align: true, disablePadding: false, numeric:"right", label: "Actions"}
];

const DataTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                  <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
};

DataTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired,
};


let DataTableToolbar = (props) => {

  const History = useHistory();
  const {AddUser, SearchUser} = props;

  return (
    <Toolbar className="table-header">
      <div className="title">
          <Typography variant="h6">Users</Typography>
      </div>
      <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
          <SearchBox placeholder="Search ..." onChange={(event) => SearchUser(event.target.value)}/>
      </div>
      <div className="actions">
        {/* <Tooltip title="Import Users">
          <IconButton className="icon-btn">
            <i className="zmdi zmdi-account-box-o"/>
          </IconButton>
        </Tooltip> */}
        <Tooltip title="Add User" onClick={()=>AddUser()}>
          <IconButton className="icon-btn">
            <i className="zmdi zmdi-account-add"/>
          </IconButton>
        </Tooltip>
        {/* <Tooltip title="Invite Users">
          <IconButton className="icon-btn">
            <i className="zmdi zmdi-account-box-phone"/>
          </IconButton>
        </Tooltip> */}
      </div>
    </Toolbar>
  );
};


const UserTable = () => {

  const [loader, setLoader] = useState(false);
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [selected, setSelected] = useState([]);
  const [data, setData] = useState([]);
  const [tabledata, setTableData] = useState([]);
  const History = useHistory();

  useEffect(() => {
    init_Data();
  }, []);

  const init_Data = () => {
    $http.get(base_url + "user/totalUserlist")
    .then((response) => {
      setData(response.data.users);
      setTableData(response.data.users);
      setCustomerRows(response.data.company);
      setLoader(true);
    });
  }

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const {width} = useSelector(({settings}) => settings);


  const [user_open, setUserOpen] = useState(false);
  const [user_id, setUserId] = useState(null);
  const [first_name,setFirstName] = useState("");
  const [last_name, setLastName] = useState("");
  const [job_title, setJobTitle] =  useState("");
  const [profile_pic, setProfilePic] = useState(null);
  const [ProfilePicUrl, setProfilePicUrl] = useState('https://via.placeholder.com/300x300');
  const [user_role, setUserRole] = useState("");
  const [contact_number, setContactNumber] = useState("");
  const [status, setStatus] = useState("");
  const [password, setPassword] = useState("");
  const [email_address, setEmailAddress] = useState("");
  const [customer_id, setCustomerId] = useState("");
  const [customer_rows, setCustomerRows] = useState([]);

  const user_roles = [
      {id: 1, content: 'Super Admin'},
      {id: 3, content: 'Account Manager'},
  ];
  const status_rows = ([
      {id: 1, content: 'Yes'},
      {id: 0, content: 'No'},
  ]);

  const handleRequestSort = (event, property) => {
    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }
    sort_order === "desc"
      ? data.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : data.sort((a, b) => (a[property] < b[property] ? -1 : 1));
    setOrder(sort_order);
    setOrderBy(property);
  };


  const handleKeyDown = (event, id) => {
    if (keycode(event) === "space") {
      handleClick(event, id);
    }
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };

  const onSelectUser = (data, index) => {
    if (index === 0) {

    } else if (index === 1) {
      UserModalOpen(data);
    } else if (index === 2) {
      $http.post(base_url + "user/deleteUser", {id: data})
      .then((response) => {
        if (response.data.status==="success") {
          init_Data();
          NotificationManager.info("You removed selected user");
        }
      })
    }
  }

  const options = ([
    {content: 'Modify', id: 1},
    {content: 'Delete', id: 2}
  ]);

  const SearchUser = (option) => {
    console.log("search ", option);
    setData(
      tabledata.filter(function (item) {
        return (item.first_name.toLowerCase().includes(option.toLowerCase()) || 
          item.last_name.toLowerCase().includes(option.toLowerCase()));
      })
    );
  }

  const UserModalOpen = (data) => {
    setUserId(data);
    if (data > 0) {
        $http.get(base_url + "user/userInfo", {params: {id: data}})
        .then((response) => {
            if (response.data.user) {
              console.log("user", response.data.user);
                setCustomerId(response.data.user.company_id);
                setFirstName(response.data.user.first_name);
                setLastName(response.data.user.last_name);
                setJobTitle(response.data.user.job_title);
                setUserRole(response.data.user.user_type);
                setContactNumber(response.data.user.mobile);
                setStatus(response.data.user.status);
                // setPassword(response.data.user.password);
                setEmailAddress(response.data.user.email);
                setProfilePicUrl(response.data.user.profile_pic ? 
                  (public_url + 'upload/img/' + response.data.user.profile_pic) : 
                  'https://via.placeholder.com/300x300');
            }
            setUserOpen(true);
        })
    } else {
        setProfilePicUrl('https://via.placeholder.com/300x300');
        setUserId(0);
        setUserOpen(true);
    }
}

  const UserModalClose = () => {
    setUserOpen(false);
  }
  const UserModalSave = () => {
    UserModalClose();
    const frmData = new FormData();
    if (user_id > 0) {
      frmData.append('id', user_id);
    }
    frmData.append('first_name', first_name);
    frmData.append('last_name', last_name);
    frmData.append('job_title', job_title);
    frmData.append('user_type', user_role);
    frmData.append('mobile', contact_number);
    frmData.append('status', status);
    frmData.append('password', password);
    frmData.append('email', email_address);
    frmData.append('profile_pic', profile_pic);
    frmData.append('customer_id', customer_id);

    $http.post(base_url + "user/updateUser", frmData)
    .then((response) => {
        if (response.data.status === "success") {
            NotificationManager.success(response.data.msg, "Success!", 5000, () => {
            });
            init_Data();
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 5000, () => {
            });
        }
    }).catch((error)=>{
        console.log("error:", error);
    })
  }
  const changeProfilePic = (e) =>{
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];
    reader.onloadend = () => {
        setProfilePic(file);
        setProfilePicUrl(reader.result);
    }
    reader.readAsDataURL(file)
  }

  return (
    
      loader ? (
    <div>
      <DataTableToolbar SearchUser={SearchUser} AddUser={() => UserModalOpen(0)}/>
      <div className="flex-auto">
        <div className="table-responsive-material">
          <Table className="">
            <DataTableHead
              order={order}
              orderBy={orderBy}
              onRequestSort={handleRequestSort}
            />
            <TableBody>
              {data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map(n => {
               
                return (
                  <TableRow
                    hover
                    // onClick={event => handleClick(event, n.id)}
                    onKeyDown={event => handleKeyDown(event, n.id)}
                    tabIndex={-1}
                    key={n.id}
                  >
                    <TableCell>{n.id}</TableCell>
                    <TableCell>
                        <div className="user-profile d-flex flex-row align-items-center">
                            <Avatar
                                alt={n.first_name}
                                src={public_url + 'upload/img/' + n.profile_pic }
                                
                                className="user-avatar"
                            />
                            <div className="user-detail">
                                <h5 className="user-name">{n.first_name} {n.last_name}</h5>
                            </div>
                        </div>
                    </TableCell>
                    <TableCell>{n.user_type}</TableCell>
                    <TableCell>{n.company_name}</TableCell>
                    <TableCell>{n.email}</TableCell>
                    <TableCell>{n.status ? 'Yes':'No'}</TableCell>
                    <TableCell className="text-right">
                        <CardEditMenu key={n.id} selectedData={n.id}
                          onSelectRow={onSelectUser} menu_rows={options}/>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
            <TableFooter>
              <TableRow>
                <TablePagination
                  count={data.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onChangePage={handleChangePage}
                  onChangeRowsPerPage={handleChangeRowsPerPage}
                />
              </TableRow>
            </TableFooter>
          </Table>
        </div>
      </div>
      <Modal key="user-save" className="modal-box" toggle={UserModalClose} isOpen={user_open}>
        <ModalHeader>{user_id ? 'Modify User':'Add User'}
            <IconButton className="text-grey"
                        onClick={UserModalClose}>
                <CloseIcon/>
            </IconButton>
        </ModalHeader>
        <div className="modal-box-content">
            <div className="row">
              <div className="col-xl-3 col-lg-4 col-md-5 col-12">
                  <div className="jr-card pb-2">
                      <div className="jr-card-thumb">
                          <img className="card-img-top img-fluid" alt="products" src={ProfilePicUrl}/>
                      </div>
                      <input type="file" id="profile_pic" accept="image/*" 
                          name="profile_pic" style={{display: "none"}}
                          onChange={(event) => changeProfilePic(event)}/>

                      <div className="jr-card-social text-right">
                          <Fab className="jr-fab-btn bg-secondary text-white jr-btn-fab-xs mb-3" 
                              onClick={(e)=>{document.getElementById('profile_pic').click()}}>
                              <i className="zmdi zmdi-cloud-upload zmdi-hc-1x"/>
                          </Fab>
                      </div>
                  </div>
              </div>
              <div className="col-xl-9 col-lg-8 col-md-7 col-12">
                <div className="row">
              <div className="col-md-6 col-12">
                    <TextField
                        id="customer_id"
                        select
                        label="Customer"
                        value={customer_id}
                        onChange={(event)=>setCustomerId(event.target.value)}
                        SelectProps={{}}
                        helperText="Please select Customer"
                        margin="normal"
                        fullWidth
                    >
                        {customer_rows.map(option => (
                            <MenuItem key={option.id} value={option.id}>
                                {option.name}
                            </MenuItem>
                        ))}
                    </TextField>
                </div>
                <div className="col-md-6 col-12">
                    <TextField 
                        id="first_name"
                        label="First Name"
                        value={first_name}
                        onChange={(event)=>setFirstName(event.target.value)}
                        margin="normal"
                        helperText="Please Enter First Name"
                        fullWidth
                        />
                </div>
                <div className="col-md-6 col-12">
                    <TextField 
                        id="last_name"
                        label="Last Name"
                        value={last_name}
                        onChange={(event)=>setLastName(event.target.value)}
                        margin="normal"
                        helperText="Please Enter Last Name"
                        fullWidth
                        />
                </div>
                <div className="col-md-6 col-12">
                    <TextField 
                        id="job_title"
                        label="Job Title"
                        value={job_title}
                        onChange={(event)=>setJobTitle(event.target.value)}
                        margin="normal"
                        helperText="Please Enter Job Title"
                        fullWidth
                        />
                </div>
                <div className="col-md-6 col-12">
                    <TextField
                        id="user_role"
                        select
                        label="User Role"
                        value={user_role}
                        onChange={(event)=>setUserRole(event.target.value)}
                        SelectProps={{}}
                        helperText="Please select User Role"
                        margin="normal"
                        fullWidth
                    >
                        {user_roles.map(option => (
                            <MenuItem key={option.id} value={option.id}>
                                {option.content}
                            </MenuItem>
                        ))}
                    </TextField>
                </div>
                <div className="col-md-6 col-12">
                    <TextField 
                        id="contact_number"
                        label="Contact Number"
                        value={contact_number}
                        onChange={(event)=>setContactNumber(event.target.value)}
                        margin="normal"
                        helperText="Please Enter Contact Number"
                        fullWidth
                        />
                </div>
                <div className="col-md-6 col-12">
                    <TextField
                        id="status"
                        select
                        label="Status"
                        value={status}
                        onChange={(event)=>setStatus(event.target.value)}
                        SelectProps={{}}
                        helperText="Please select Status"
                        margin="normal"
                        fullWidth
                    >
                        {status_rows.map(option => (
                            <MenuItem key={option.id} value={option.id}>
                                {option.content}
                            </MenuItem>
                        ))}
                    </TextField>
                </div>
                <div className="col-md-6 col-12">
                    <TextField 
                        id="password"
                        label="Password"
                        value={password}
                        onChange={(event)=>setPassword(event.target.value)}
                        margin="normal"
                        helperText="Please Enter Password"
                        fullWidth
                        />
                </div>
                <div className="col-md-6 col-12">
                    <TextField 
                        id="email_address"
                        label="Email Address"
                        value={email_address}
                        onChange={(event)=>setEmailAddress(event.target.value)}
                        margin="normal"
                        helperText="Please Enter Email Address"
                        fullWidth
                        />
                </div>
              </div></div>
            </div>
        </div>
        <div className="modal-box-footer d-flex flex-row">
            <Button onClick={UserModalClose} color="secondary">
            Cancel
            </Button>
            <Button onClick={UserModalSave} color="primary">
            Save
            </Button>
        </div>
      </Modal>
      <NotificationContainer/>
    </div>
    ):(
      <div className="loader-view"
            style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
        <CircularProgress/>
      </div>
    )
  );
};

export default UserTable;
